.rectangleHomeContent {
    border: 1px solid red;
    padding: 30px;
    width: 100%;
}

.rectangleHomeContent ul {
    list-style: none;
}

.rectangleHomeContent ul li {
    display: inline;
    float: left;
    clear: left;
}

.principalImage {
    float: left;
    border-width: 2px;
    border-style: solid;
    margin: 25px;
    width: 137px;
}

.principalDescription {
    float: left;
    border-width: 1px;
    border-style: solid;
    padding: 1px;
    margin: 15px;
    width: 157px;
    height: 80px
}